.gallery-title{
    font-size: $h2-font-size;
    margin-bottom: 20px;
}
.filter-button{
    font-size: $h6-font-size;
    border: 2px solid #3F6184;
    padding:5px 10px;
    color: #3F6184;
    margin-bottom: 10px;
    background:transparent;
}
.filter-button:hover,
.filter-button:focus,
.filter-button.active{
    color: #ffffff;
    background-color:#3F6184;
    outline:none;
}
.gallery_product{
    margin: 0px;
    padding:0;
    position:relative;
}

.gallery_product img { 
    max-height: 400px;
    aspect-ratio: 1;
    object-fit: cover;
} 

.gallery_product .img-info{
    position: absolute;
    background: rgba(0,0,0,0.5);
    left: 0;
    right: 0;
    bottom: 0;
    padding: 20px;
    overflow:hidden;
    color:#fff;
    top:0;
    display:none;
    -webkit-transition: 2s;
    transition: 2s;
}

.gallery_product:hover .img-info{
    display:block;
    -webkit-transition: 2s;
    transition: 2s;
}