.navbar {
     border-bottom: 0px solid rgba($themecolor, 1);
     box-shadow: 0 2px 4px 0 rgba($primary, .2);
}

.navbar-brand {
  font-size: $font-size-base * 2;
  color: $black;
  &:hover,
  &:focus {
    color: $themecolor;
  }
}

.navbar-nav {
  font-size: $font-size-base * 1.2;
  color: $black;
  .show > .nav-link,
  .nav-link.active {
    color: $themecolor;
  }
}
.nav-link {
  font-size: $font-size-base * 1.2;
  color: $black;
  &:hover,
  &:active,
  &:focus {
    color: $themecolor;
  }
}

.navbar-text {
  font-size: $font-size-base * 1.2;
  color: $black;
  a,
  a:active,
  a:hover,
  a:focus {
    color: $themecolor;
  }
}

.py-custom {
     padding-top: ($spacer * .2) !important;
     padding-bottom: ($spacer * .35) !important;
}

.nav-title {
     margin-left: 75px;
     padding-top: ($spacer * .2) !important;
}

@media (max-width: 1024px) {
  .nav-title { display: none; }
}