.proj {

    .accordion-item {
        border: none;
    }

    .accordion-button {
        background-color: $white !important;
        @include font-size($h3-font-size);

        &:focus {
            border-color: none;
            box-shadow: none;
        }

        &:not(.collapsed) {
            box-shadow: none;
        }
    }

    .jumbotron {
        padding:10px 10px 10px 10px;
        margin-top:15px;
        margin-bottom:15px;
        background-color: $white !important;
        border-radius: 0px;
        box-shadow: 0px 0px 0px #888888;
        border: 1px;
        border-color: #666;
        border-style: solid;
    }
}

.presentation {

    .accordion-item {
        border: none;
    }

    .accordion-button {
        background-color: $white !important;
        @include font-size($h4-font-size);

        &:focus {
            border-color: none;
            box-shadow: none;
        }

        &:not(.collapsed) {
            box-shadow: none;
        }
    }

    .img-max {
        max-width: 750px;
        width: 100%;
    }
}
