html {
    font-size: 16px;
    scroll-padding-top: 80px;
}

body {
    font-family: 'EB Garamond', serif;
}

figcaption {
    color: #222;
    font: italic smaller sans-serif;
    padding: 3px;
    text-align: left;
}

/* class selector */
/* padding is a shorthand for padding-top, padding-right, padding-bottom, padding-left */
/* box-shadow: offset-x | offset-y | blur-radius | color */

.jumbotron{
    padding:10px 10px 10px 10px;
    margin-top:15px;
    margin-bottom:15px;
    background-color: rgba($themecolor, 0.1);
    border-radius: 5px;
    box-shadow: 2px 2px 5px #888888
}

.container-fluid {
    margin-top: 15px;
    margin-bottom: 15px;
    margin-right: auto;
    margin-left: auto;
    max-width: 1200px;
}

.footer-clean {
    padding:50px 0;
    background-color:$primary;
    color: #fff;
}

.footer-clean h3 {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    font-size:16px;
    color: #fff;
}

.footer-clean a {
    color: $gray-500;
}

.footer-clean a:hover {
    color: #fff;
}

.footer-clean ul {
    padding:0;
    list-style:none;
    line-height:1.6;
    font-size:14px;
    margin-bottom:0;
    color: #0a0a0a;
}

.footer-clean ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.8;
}

.footer-clean ul a:hover {
    opacity:1;
}

.footer-clean .item.social {
    text-align:center;
}

@media (max-width:767px) {
    .footer-clean .item {
        text-align:center;
        padding-bottom:20px;
    }
}

@media (max-width: 768px) {
    .footer-clean .item.social {
        text-align:center;
    }
}

.footer-clean .item.social > a {
    font-size:24px;
    width:40px;
    height:40px;
    line-height:40px;
    display:inline-block;
    text-align:center;
    border-radius:50%;
    border:1px solid #ccc;
    margin-left:10px;
    margin-top:22px;
    color:inherit;
    opacity:0.5;
}

.footer-clean .item.social > a:hover {
    opacity:1;
}

@media (max-width:991px) {
    .footer-clean .item.social > a {
        margin-top:40px;
    }
}

@media (max-width:767px) {
    .footer-clean .item.social > a {
        margin-top:10px;
    }
}

.footer-clean .copyright {
    margin-top:14px;
    margin-bottom:0;
    font-size:13px;
    opacity:0.9;
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}


/* single element selector */

#btn-back-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: none;
}

#btn-back-to-top:hover {
    background-color: rgba($themecolor, 0.9); /* Add a background on hover */
}

.cssbuttons-io-button {
 background: rgba($themecolor, 0.4);
 color: white;
 display: inline-flex;
 padding: 0.35em;
 padding-left: 1.2em;
 font-size: $small-font-size*0.9;
 font-weight: 500;
 border-radius: 0.9em;
 border: none;
 letter-spacing: 0.05em;
 align-items: center;
 box-shadow: inset 0 0 1.6em -0.6em #714da6;
 overflow: hidden;
 position: fixed;
 top: 35vh;
 right: 0;
 height: 2.8em;
 padding-right: 3.3em;
}

@media (max-width:991px) {
    .cssbuttons-io-button {
        font-size: $small-font-size*0.7;
        top: 50vh;
    }
}

.cssbuttons-io-button .icon {
 background: white;
 display: inline-flex;
 margin-left: 1em;
 position: absolute;
 align-items: center;
 justify-content: center;
 height: 2.2em;
 width: 2.2em;
 border-radius: 0.7em;
 box-shadow: 0.1em 0.1em 0.6em 0.2em #7b52b9;
 right: 0.3em;
 transition: all 0.3s;
}

.cssbuttons-io-button:hover .icon {
 width: calc(100% - 0.6em);
}

.cssbuttons-io-button .icon svg {
 width: 1.1em;
 transition: transform 0.3s;
 color: $themecolor;
}

.cssbuttons-io-button:hover .icon svg {
 transform: translateX(0.1em);
}

.cssbuttons-io-button:active .icon {
 transform: scale(0.95);
}

/* Home, News */
#newsid p {
    text-align: left;
}

#newsid a {
    color: $warning;
}

#blogid p {
    text-align: left;
}

#blogid a {
