#homeid p {
    padding: 0 0 10px 0;
}

/* Team */
.album { 
    display: flex; 
    align-items: center; 
    justify-content: start; 
} 

.imgContainer { 
    overflow: hidden; 
} 

.imgContainer img { 
    max-height: 400px;
    aspect-ratio: 3/4;
    object-fit: cover;
} 

/* Squared Img and Hover Effect Where Needed */
.ibox
{
    position: relative;
    max-height: 300px;
    aspect-ratio: 1;
    overflow: hidden;
    border-radius: 10px;
    background: #000;
    box-shadow: 0 5px 15px rgba(0,0,0,.5);
    transition: .5s;
    margin-bottom: 35px;
}

.ibox:hover
{
    transform: translateY(-30px);
    box-shadow: 0 50px 50x rgba(0,0,0,.5);
}

.ibox .imgbox
{
    position: relative;
}

.ibox .imgbox img
{
    transition: .5s;
}

.ibox:hover .imgbox img
{
   opacity: .5;
    transform: translateY(-40px);
}

.ibox .content
{
    position: absolute;
    bottom: -40px;
    left: 0;
    padding: 20px;
    box-sizing: border-box;
    transition: .5s;
    opacity: 0;
    
}

.ibox:hover .content
{
    opacity: 1;
    bottom: 0;
}

.ibox .content h3
{
    color: #fff;
    font-weight: 700;
}

.ibox .content p
{
    font-size: 18px;
    color: #fff;
    font-weight: 400;
}

.ibox .content .btnD
{
    border: none;
    background: #ff0000;
    color: #fff;
    font-size: 18px;
    padding: 10px 20px;
    font-weight: 700;
    transition: .5s;
}

.ibox .content .btnD:hover
{
    background: #ff3232;
    
}

.blink_me {
  animation: blinker 2s linear infinite;
  -webkit-animation: blinker 2s linear infinite;
  -moz-animation: blinker 2s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@-moz-keyframes blinker {
  50% {
    opacity: 0;
  }
}

