.post{

    h2 {
        margin: 30px 0 30px 0;
    }

    h5 {
        margin-bottom: 20px;
    }

    #post header {
        display: grid;
        grid-template-columns: auto auto auto auto 1fr;
        align-items: center;
        grid-gap: 15px;
        margin-bottom: 30px;
        width: 100%;
    }

    .author {
    }

    .date {
    }

    .reading-time {
    }

    .reading-time count {
    }

    .reading-time timer {
    }

    .sharing {
        justify-self: end;
    }

    .social i {
        padding-right: 10px;
        font-size: 1.5em;
    }

    @media screen and (max-width: 600px) {
        #post header {
            grid-template-columns: auto;
        }
        .sharing {
            justify-self: start;
        }
    }
}

#blogid {
}

#blogid .blog-post {
  -webkit-transition: all 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0s;
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0s;
}

#blogid .blog-post .blog-img .overlay,
#blogid .blog-post .blog-img .post-meta {
  position: absolute;
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#blogid .blog-post .blog-img .overlay {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

#blogid .blog-post .blog-img .post-meta {
  bottom: 5%;
  right: 5%;
  z-index: 1;
}

#blogid .blog-post .blog-img .post-meta .read-more:hover {
  color: #6dc77a !important;
}

#blogid .blog-post .content h1, .blog-post .content h2, .blog-post .content h3, .blog-post .content h4, .blog-post .content h5, .blog-post .content h6 {
  line-height: 1.2;
}

#blogid .blog-post .content .title {
  font-size: 18px;
}

#blogid .blog-post .content .title:hover {
  color: #6dc77a !important;
}

#blogid .blog-post .content .author .name:hover {
  color: #6dc77a !important;
}

#blogid .blog-post:hover {
  -webkit-transform: translateY(-7px);
          transform: translateY(-7px);
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
}

#blogid .blog-post:hover .blog-img .overlay {
  opacity: 0.65;
}

#blogid .blog-post:hover .blog-img .post-meta {
  opacity: 1;
}

#blogid .blog-post .post-meta .like i,
.profile-post .like i {
  -webkit-text-stroke: 2px #dd2427;
  -webkit-text-fill-color: transparent;
}

#blogid .blog-post .post-meta .like:active i, .blog-post .post-meta .like:focus i,
.profile-post .like:active i,
.profile-post .like:focus i {
  -webkit-text-stroke: 0px #dd2427;
  -webkit-text-fill-color: #dd2427;
}

#blogid .avatar.avatar-ex-sm {
    height: 36px;
}

.shadow {
    -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15) !important;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.15) !important;
}

.para-desc {
    max-width: 600px;
}
