.hiddenbox > .icon { position: relative; text-align: center}
.hiddenbox > .icon > .image {
    width: 50px;
    height: 50px;
    position: absolute; 
    background: #eee;
    border-radius: 50%;
    margin: auto;
    line-height: 50px;
    color: $primary !important;
    transition: .5s;
}
.hiddenbox > .icon > .image::before {
    content: '';
    position: absolute; 
    background: $primary !important;
    border-radius: 50%;
    transition: .5s;
    transform: scale(.9);
    z-index: -1;
}

.hiddenbox > .icon:hover > .image::before {
    transform: scale(1.1);
    box-shadow: 0 0 15px $primary !important;
}

.hiddenbox > .icon:hover > .image {
    color: $primary !important;
    box-shadow: 0 0 15px $primary !important;
    text-shadow: 0 0 15px $primary !important;
}

.hiddenbox > .icon > .image > i::before {
    transform: scale(1);
    transition: .5s;
    font-size: $h3-font-size !important;
    line-height: 50px;
}
.hiddenbox > .icon:hover > .image > i::before {
    color: $primary !important;
    transform: scale(1.2);
    text-shadow: 0 0 15px $primary !important;
}
.hiddenbox > .icon:hover > .image > i {
    color: #ffee10;
    text-shadow: 0 0 15px $primary !important;
}

.hiddenbox > .icon > .info { border: 0px solid #e0e0e0; padding: 0px 0 0px 0px; }
/* .hiddenbox > .icon:hover > .info { background: rgba($themecolor, 0.1); border-color: #e0e0e0; color: white; } */
.hiddenbox > .icon > .info > .title { font-size: $h4-font-size; color: $primary !important; font-weight: 500; text-align: left; vertical-align: middle; padding: 5px 0 0px 60px;}
.hiddenbox > .icon > .info > h3.text { font-size: $h5-font-size; color: #222; font-weight: 500; text-align: left; padding: 20px 0 0px 0px;}
.hiddenbox > .icon > .info > p {font-size: $font-size-base; color: #666; line-height: 1.5em; margin: 20px; text-align: left;}
/* .hiddenbox > .icon:hover > .info > h3.title, .hiddenbox > .icon:hover > .info > p, .hiddenbox > .icon:hover > .info > .more > a { color: #222; } */
.hiddenbox > .icon > .info > .more a {font-size: $font-size-base; color: #222; line-height: 12px; text-transform: uppercase; text-decoration: none; }
.hiddenbox > .icon:hover > .info > .more > a { color: #fff; padding: 6px 8px; background-color: rgba($themecolor, 0.3); }
.hiddenbox .space { height: 30px; }
