#recentnews {
}

#recentnews .blog-grid {
    margin-top: 15px;
    margin-bottom: 15px;
}

#recentnews .blog-grid .blog-img {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}

#recentnews .blog-grid .blog-img .date {
    position: absolute;
    background: $themecolor;
    color: #ffffff;
    padding: 8px 15px;
    left: 0;
    top: 10px;
    font-size: 14px;
}

#recentnews .blog-grid .blog-info {
    box-shadow: 0 0 30px rgba(31, 45, 61, 0.125);
    border-radius: 5px;
    background: #ffffff;
    padding: 20px;
    margin: -30px 20px 0;
    position: relative;
}

#recentnews .blog-grid .blog-info h5 {
    margin: 0 0 10px;
}

#recentnews .blog-grid .blog-info h5 a {
    color: $body-color;
}

#recentnews .blog-grid .blog-info h5 a:hover {
    color: $themecolor;
}

#recentnews .blog-grid .blog-info p {
    margin: 0;
}

#recentnews .blog-grid .blog-info .btn-bar {
    margin-top: 20px;
}

.px-btn-arrow {
    padding: 0 50px 0 0;
    line-height: 20px;
    position: relative;
    display: inline-block;
    color: currentColor;
    -moz-transition: ease all 0.3s;
    -o-transition: ease all 0.3s;
    -webkit-transition: ease all 0.3s;
    transition: ease all 0.3s;
}

.px-btn-arrow .arrow {
    width: 13px;
    height: 2px;
    background: currentColor;
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 25px;
    -moz-transition: ease right 0.3s;
    -o-transition: ease right 0.3s;
    -webkit-transition: ease right 0.3s;
    transition: ease right 0.3s;
}

.px-btn-arrow .arrow:after {
    width: 8px;
    height: 8px;
    border-right: 2px solid currentColor;
    border-top: 2px solid currentColor;
    content: "";
    position: absolute;
    top: -3px;
    right: 0;
    display: inline-block;
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}


/* animation for news on hero */
.newshero{
    .wrapper{
        position: absolute;
        top: 10vh;
        left: 50vw;
        transform: translate(-50%, 0);
    }

    a{
        display: block;
        font-size: $h3-font-size;
        text-decoration: none;
        color: $themecolor;
        background: rgba($body-color, 0.35);
        border-top: 3px solid $themecolor;
        border-bottom: 3px solid $themecolor;
        padding: 5px;
        letter-spacing: 2px;
        transition: all .25s;
    }

    a:hover{
        letter-spacing: 10px;
    }

    @media (max-width: 992px) {
        a {
            font-size: $h6-font-size;
        }
        a:hover{
            letter-spacing: 5px;
        }
    }
}
