/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/
#hero {
    width: 100%;
    height: 90vh;
    margin-bottom: -220px;
}

#hero .container-fluid {
    position: relative;
    padding: 0;
    margin: 0 0 0 0;
    max-width: 100%;
}

#hero h1 {
    margin: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
}

#hero h2 {
    color: #fff;
    font-size: $h4-font-size;
    margin: 10px 0 0 10%;
}

#hero .btn-get-started {
    text-transform: uppercase;
    letter-spacing: 1px;
    display: inline-block;
    padding: 12px 35px;
    margin-top: 30px;
    margin-left: 10%;
    border-radius: 50px;
    transition: 0.5s;
    color: #fff;
    background: $body-color;
}

#hero .btn-get-started:hover {
    background: #fff;
    color: $body-color;
}

@media (min-width: 1024px) {
    #hero {
        background-attachment: fixed;
    }
}

@media (max-width: 992px) {
    #hero {
        margin-bottom: 0;
        height: 100vh;
    }

    #hero .container {
        padding-bottom: 63px;
    }

    #hero h1 {
        font-size: $h3-font-size;
        line-height: 36px;
    }

    #hero h2 {
        font-size: $h5-font-size;
        line-height: 24px;
        margin-bottom: 30px;
        margin-left: 0;
        text-align: center;
    }
}

@media (max-height: 600px) {
    #hero {
        height: 110vh;
    }
}

/*--------------------------------------------------------------
# Hero Section for People Page
--------------------------------------------------------------*/
#herop::before {
    content: "";
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
}

#herop {
    width: 100%;
    height: 90vh;
    background-position: top right;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}

#herop .hero-container {
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    padding: 0 10vw;
}

@media (min-width: 992px) {
    #herop {
        padding-left: 160px;
    }
}

#herop h1 {
    margin: 0;
    line-height: 56px;
    color: #45505b;
}

#herop p {
    color: #45505b;
    margin: 15px 0 0 0;
    font-size: $h5-font-size;
}

#herop p span {
    color: #45505b;
    letter-spacing: 1px;
}

#herop .social-links {
    margin-top: 30px;
    margin-bottom: 15px;
}

#herop .social-links a {
    font-size: $small-font-size;
    display: inline-block;
    color: #45505b;
    line-height: 1;
    margin-right: 20px;
    transition: 0.3s;
}

#herop .social-links a:hover {
    color: $themecolor;
}

@media (max-width: 992px) {
    #herop {
        text-align: center;
    }

    #herop h1 {
        font-size: $h2-font-size;
        line-height: 36px;
    }

    #herop p {
        margin-top: 10px;
        line-height: 24px;
    }

    #herop .social-links a {
        font-size: $small-font-size;
    }

}

#herop .btn-scroll {
    font-size: $small-font-size;
    transition: 0.4s;
    color: $body-color;
    animation: up-down 1s ease-in-out infinite alternate-reverse both;
}

#herop .btn-scroll i {
    font-size: $h4-font-size;
}

#herop .btn-scroll:hover {
    color: $themecolor;
}

@keyframes up-down {
  0% {
    transform: translateY(8px);
  }

  100% {
    transform: translateY(-8px);
  }
}