.box > .icon { text-align: center; position: relative; }
.box > .icon > .image { position: relative; margin: auto; width: 88px; height: 88px; border: 8px solid white; line-height: 88px; border-radius: 50%; background: rgba($themecolor, 1); vertical-align: middle; }
/* .box > .icon:hover > .image { background: #333; } */
.box > .icon > .image > i { font-size: 36px !important; color: #fff !important; }
/* .box > .icon:hover > .image > i { color: white !important; } */
.box > .icon > .info { margin-top: -24px; background: rgba($themecolor, 0.1); border: 1px solid #e0e0e0; padding: 15px 0 10px 0; }
/* .box > .icon:hover > .info { background: rgba($themecolor, 0.1); border-color: #e0e0e0; color: white; } */
.box > .icon > .info > h3.title { font-size: $h5-font-size; color: #222; font-weight: 500; text-align: left; padding: 15px 0 0 15px;}
.box > .icon > .info > p {font-size: $font-size-base; color: #666; line-height: 1.5em; margin: 20px; text-align: left;}
/* .box > .icon:hover > .info > h3.title, .box > .icon:hover > .info > p, .box > .icon:hover > .info > .more > a { color: #222; } */
.box > .icon > .info > .more a {font-size: $font-size-base; color: #222; line-height: 12px; text-transform: uppercase; text-decoration: none; }
.box > .icon:hover > .info > .more > a { color: #fff; padding: 6px 8px; background-color: rgba($themecolor, 0.3); }
.box .space { height: 30px; }
