/*--------------------------------------------------------------
# Resume
--------------------------------------------------------------*/
.resume .resume-title {
  font-size: $h3-font-size;
  font-weight: 700;
  margin-top: 20px;
  margin-bottom: 20px;
}

.resume .resume-item {
  padding: 0 0 20px 20px;
  margin-top: -2px;
  border-left: 2px solid $themecolor;
  position: relative;
}

.resume .resume-item h4 {
  line-height: $h4-font-size;
  font-size: $h4-font-size;
  font-weight: 400;
  margin-bottom: 10px;
}

.resume .resume-item h5 {
  font-size: $h5-font-size;
  background: rgba($themecolor, 0.3);
  padding: 5px 15px;
  display: inline-block;
  font-weight: 400;
  margin-bottom: 10px;
}

.resume .resume-item ul {
  padding-left: 20px;
}

.resume .resume-item ul li {
  padding-bottom: 10px;
}

.resume .resume-item:last-child {
  padding-bottom: 0;
}

.resume .resume-item::before {
  content: "";
  position: absolute;
  width: $h4-font-size;
  height: $h4-font-size;
  border-radius: 50px;
  left: $h4-font-size * -0.53;
  top: 0;
  background: #fff;
  border: 2px solid $themecolor;
}
